// OTP Dorulama ve Skorlama
// Web Servis Ad: ValidateOTPCreateApp
// Fonksiyon: "Devam Et" butonuna basldnda, mterinin ekrana girdii OTP ile cep telefonuna gnderilen OTP'nin doruluu karlatrlr.
// Doru ise n skorlama yaplarak bavuru yaratlr.
// Output	Parametre	Aklama
// Output -> otpResult -> OTP Karlatrma Sonucu
// Output -> successUrl -> Baarl URL
// Output -> failUrl -> Baarsz URL
// Output -> approvalStatus -> Skorlama Sonucu
// Output -> explanation -> Skorlama Sonucu Aklama
// Output -> applicationNo -> Oluan Bavuru Numaras
// Output -> resultCode -> Sonu Kodu
// Output -> resultExplanation -> Sonu Aklamas
export async function ValidateOTP({
 transactionId = null, // leme ait tekil numara
 channelCode = null, // D Sistem Kanal Kodu
 companyCode = null, // Kurum Kodu
 nationalIdentityNo = null, // TCKN
 mobileNumber = null, // Cep Telefonu
 otp = null, // Ekrana Girilen OTP
 email = null, // Ekrana Girilen E-posta
 basketAmount = null, // Sepet Tutar
 referenceCode = null, // Referans Kod-1
 adkOid = null, // Referans Kod-2
 orderId = null, // Sipari Numaras
 productTable = [] // Sepetteki rnler
} = {}) {
 return serviceStep1
  .post('ValidateOTPCreateApp', {
   transactionId,
   channelCode,
   companyCode,
   nationalIdentityNo,
   mobileNumber,
   otp,
   email,
   basketAmount,
   referenceCode,
   adkOid,
   orderId,
   productTable
  })
  .then(globalServiceHandler)
}

